export class TextLabelFactory {

  private static instances: Map<string, TextLabelFactory> = null

  private gl: WebGLRenderingContext

  private _font: Map<string, FontSDF>

  private constructor(gl: WebGLRenderingContext) {
    this.gl = gl
    this._font = new Map<string, FontSDF>()
    this.init()
  }

  public static getInstance(gl: WebGLRenderingContext) {
    let id = gl["id"]
    if (this.instances == null) {
      this.instances = new Map<string, TextLabelFactory>()
    }
    let instance = this.instances.get(id)
    if (!instance) {
      instance = new this(gl)
      this.instances.set(id, instance)
    }
    return instance
  }

  public static releaseInstance(gl: WebGLRenderingContext) {
    let id = gl["id"]
    if (this.instances == null) {
      return
    }
    let instance = this.instances.get(id)
    if (!instance) {
      return
    }
    instance._font.forEach(font => font.destroy())
    instance._font.clear()
    this.instances.delete(id)
  }

  public init(): void {
    let normalFont = new FontSDF(this.gl, FontConstant.fontSize, 0.14, "Arial", 300)
    this._font.set("normal", normalFont)
    let boldFont = new FontSDF(this.gl, FontConstant.fontSize, 0.14, "Arial", 600)
    this._font.set("bold", boldFont)
  }

  public createCurvedText(textLabel: TextLabel, fontWeight: string, path: Point[], metersPerPixel: number): TextLabelData {
    if (path == null || path.length < 2) {
      return this.createText(textLabel, fontWeight, 200, 1)
    }
    let font = this._font.get(fontWeight)
    if (!font) {
      console.error("Can not find this Font to create text: " + textLabel.text)
      return null
    }
    let gl = this.gl
    let vertexElements = []
    let text = textLabel.text
    let size = textLabel.size
    let fontSize = font.fontSize
    let scale = size / fontSize
    let pen = { x: 0, y: 0}
    let buffer = font.tinySDFGenerator.buffer
    let width = fontSize + buffer * 2
    let height = fontSize + buffer * 2
    let horiBearingX = 0
    let horiBearingY = fontSize / 2 + buffer
    let currentPosition = 1
    path.forEach(point => {
      point.x /= metersPerPixel
      point.y /= metersPerPixel
    })
    path = simplify(path) as Point[]
    let direction = new Point((path[currentPosition].x - path[currentPosition - 1].x),
      (path[currentPosition].y - path[currentPosition - 1].y))
    let currentLength = 0
    let currentPathLength = Math.sqrt(Math.pow(direction.x, 2) + Math.pow(direction.y, 2))
    let degreeInRadians = (direction.y >= 0 ? 1 : -1) * Math.acos(direction.x / currentPathLength)
    let origin = new Point(0, 0)
    for (let i = 0; i < text.length; i++) {
      let character = text[i]
      let metric = font.metrics[character]
      if (!metric) {
        continue
      }
      let horiAdvance = horiAdvances[character]
      let characterLength = horiAdvance * scale
      currentLength += characterLength

      while (currentLength >= currentPathLength + characterLength / 3 && currentPosition < path.length - 1) {

        origin.x += Math.cos(degreeInRadians) * currentPathLength
        origin.y += Math.sin(degreeInRadians) * currentPathLength
        if (character == " ") {
          pen.x = origin.x
        }
        else {
          pen.x = origin.x  + characterLength / 2
        }
        pen.y = origin.y

        // Update new path
        ++currentPosition
        direction = new Point((path[currentPosition].x - path[currentPosition - 1].x),
          (path[currentPosition].y - path[currentPosition - 1].y))
        currentLength = horiAdvance * scale
        currentPathLength = Math.sqrt(Math.pow(direction.x, 2) + Math.pow(direction.y, 2))
        degreeInRadians = (direction.y >= 0 ? 1 : -1) * Math.acos(direction.x / currentPathLength)
      }

      let posX = metric.x
      let posY = metric.y

      if (width > 0 && height > 0) {
        let point1 = new Point(pen.x + (horiBearingX - buffer) * scale, pen.y - horiBearingY * scale)
        let point2 = new Point(pen.x + (horiBearingX - buffer) * scale, pen.y + (height - horiBearingY) * scale)
        let point3 = new Point(pen.x + (horiBearingX - buffer + width) * scale, pen.y - horiBearingY * scale)
        let point4 = new Point(pen.x + (horiBearingX - buffer + width) * scale,
          pen.y + (height - horiBearingY) * scale)

        let x1 = ((point1.x - origin.x) * Math.cos(degreeInRadians) - (point1.y - origin.y) * Math.sin(degreeInRadians)) + origin.x
        let y1 = ((point1.x - origin.x) * Math.sin(degreeInRadians) + (point1.y - origin.y) * Math.cos(degreeInRadians)) + origin.y
        let x2 = ((point2.x - origin.x) * Math.cos(degreeInRadians) - (point2.y - origin.y) * Math.sin(degreeInRadians)) + origin.x
        let y2 = ((point2.x - origin.x) * Math.sin(degreeInRadians) + (point2.y - origin.y) * Math.cos(degreeInRadians)) + origin.y
        let x3 = ((point3.x - origin.x) * Math.cos(degreeInRadians) - (point3.y - origin.y) * Math.sin(degreeInRadians)) + origin.x
        let y3 = ((point3.x - origin.x) * Math.sin(degreeInRadians) + (point3.y - origin.y) * Math.cos(degreeInRadians)) + origin.y
        let x4 = ((point4.x - origin.x) * Math.cos(degreeInRadians) - (point4.y - origin.y) * Math.sin(degreeInRadians)) + origin.x
        let y4 = ((point4.x - origin.x) * Math.sin(degreeInRadians) + (point4.y - origin.y) * Math.cos(degreeInRadians)) + origin.y

        vertexElements.push(
          x1, y1, posX, posY + height,
          x3, y3, posX + width, posY + height,
          x2, y2, posX, posY,

          x3, y3, posX + width, posY + height,
          x2, y2, posX, posY,
          x4, y4, posX + width, posY)
        pen.x += horiAdvance * scale
      }
    }

    let model = new Model(gl)
    let arrayBuffer = gl.createBuffer()
    gl.bindBuffer(gl.ARRAY_BUFFER, arrayBuffer)
    gl.bufferData(gl.ARRAY_BUFFER, new Float32Array(vertexElements), gl.STATIC_DRAW)
    gl.bindBuffer(gl.ARRAY_BUFFER, null)
    model.arrayBuffer = arrayBuffer
    model.numOfVertices = vertexElements.length / 4
    model.attributeInfos = [
      new AttributeInfo("a_position", 2, 0, 4),
      new AttributeInfo("a_uv", 2, 2, 4)
    ]
    model.retain()
    return new TextLabelData(textLabel.text, font, size, width, height, MapConstants.none, model)
  }

  public createText(textLabel: TextLabel, fontWeight: string, maxWidth: number, line: number): TextLabelData {
    let font = this._font.get(fontWeight)
    if (!font) {
      console.error("Can not find this Font to create text: " + textLabel.text)
      return null
    }
    let gl = this.gl
    let vertexElements = []
    let text = textLabel.text
    let size = textLabel.size
    let textLength = this.measureTextLabelLength(textLabel)
    let cancelThreeDot = false
    let alignCenter = true
    let lineEstimate = Math.ceil(textLength / maxWidth)
    if (line == 0 || line > lineEstimate) {
      if (line > 0) {
        alignCenter = false
        cancelThreeDot = true
      }
      line = lineEstimate
    }
    let widthPerLine = maxWidth
    let width = maxWidth
    if (line == 1 && textLength < maxWidth) {
      width = textLength
      if (alignCenter) {
        widthPerLine = textLength
      }
      cancelThreeDot = true
    }

    let pen = { x: 0 - widthPerLine / 2.0, y: (size / 2.0) - (size * line / 2.0)}
    let textCurrentLine = ""
    let currentLineNumber = 1.0
    for (let i = 0; i < text.length; i++) {
      let chr = text[i]
      textCurrentLine = textCurrentLine + chr
      let breakNewLine = false
      if (chr == " ") {
        let nextWord = ""
        let j = i + 1
        while (text[j] != " " && j < text.length) {
          nextWord = nextWord + text[j]
          ++j
        }
        let currentTextWithNextWordLength = this.measureTextLength(textCurrentLine + nextWord, size)
        if (currentLineNumber == line && widthPerLine < currentTextWithNextWordLength
          && widthPerLine - currentTextWithNextWordLength < 3 * this.measureTextLength(".", size) && !cancelThreeDot) {
          this.drawGlyph(".", pen, font, size, widthPerLine, vertexElements, false)
          this.drawGlyph(".", pen, font, size, widthPerLine, vertexElements, false)
          this.drawGlyph(".", pen, font, size, widthPerLine, vertexElements, false)
          break
        }
        else if (currentTextWithNextWordLength > widthPerLine) {
          textCurrentLine = ""
          breakNewLine = true
          ++currentLineNumber
        }
      }
      this.drawGlyph(chr, pen, font, size, widthPerLine, vertexElements, breakNewLine)
    }

    let model = new Model(gl)
    let buffer = gl.createBuffer()
    gl.bindBuffer(gl.ARRAY_BUFFER, buffer)
    gl.bufferData(gl.ARRAY_BUFFER, new Float32Array(vertexElements), gl.STATIC_DRAW)
    gl.bindBuffer(gl.ARRAY_BUFFER, null)
    model.arrayBuffer = buffer
    model.numOfVertices = vertexElements.length / 4
    model.attributeInfos = [
      new AttributeInfo("a_position", 2, 0, 4),
      new AttributeInfo("a_uv", 2, 2, 4)
    ]
    model.retain()
    return new TextLabelData(textLabel.text, font, size, width, currentLineNumber * size, widthPerLine, model)
  }

  private drawGlyph(chr, pen, font, size, textWidthPerLine, vertexElements, breakNewLine): void {
    let metric = font.metrics[chr]
    if (!metric) return

    let fontSize = font.fontSize
    let scale = size / fontSize
    let buffer = font.tinySDFGenerator.buffer
    let width = fontSize + buffer * 2
    let height = fontSize + buffer * 2
    let horiBearingX = 0
    let horiBearingY = fontSize / 2 + buffer
    let horiAdvance = horiAdvances[chr]
    let posX = metric.x
    let posY = metric.y

    if (width > 0 && height > 0) {

      let x1 = pen.x + (horiBearingX - buffer) * scale
      let x2 = pen.x + (horiBearingX - buffer + width) * scale
      let y1 = pen.y - horiBearingY * scale
      let y2 = pen.y + (height - horiBearingY) * scale

      vertexElements.push(
        x1, y1, posX, posY,
        x2, y1, posX + width, posY,
        x1, y2, posX, posY + height,

        x2, y1, posX + width, posY,
        x1, y2, posX, posY + height,
        x2, y2, posX + width, posY + height
      )
    }

    pen.x = pen.x + horiAdvance * scale
    if (breakNewLine) {
      pen.y += size
      pen.x = 0 - textWidthPerLine / 2.0
    }
  }

  private measureTextLabelLength(textLabel: TextLabel): number {
    let length = 0
    let size = textLabel.size
    let text = textLabel.text
    let fontSize = FontConstant.fontSize

    let scale = size / fontSize
    for (let i = 0; i < text.length; i++) {
      let horiAdvance = horiAdvances[text[i]]
      length += horiAdvance * scale
    }
    return length
  }

  private measureTextLength(text: String, size: number): number {
    let length = 0
    let fontSize = FontConstant.fontSize

    let scale = size / fontSize
    for (let i = 0; i < text.length; i++) {
      let horiAdvance = horiAdvances[text[i]]
      length += horiAdvance * scale
    }
    return length
  }
}
